}
.navbar{
    position: relative;
    margin:0 auto;
    display:grid;
    grid-template-columns:repeat(3,4fr);
    width:95%;
    height:35px;
    align-items: center;
    padding-top:10px;
    font-family: 'Poppins', sans-serif;
}
.navbar-brand{
    max-width:150px;
    position: relative;
    top:5px;
}
.navbar-middle{
    display:grid;
    max-width:470px;
    grid-template-columns:repeat(4,1fr);
    justify-self: center;
    justify-items: center;
    font-size:.9em;
}
.navbar-middle .home::before{
    content:'/';
    position: relative;
    font-size:10pt;
    bottom:2px;
    margin-right:3px;
    color:#E84C9B;
    font-weight: bold;
}  
.navbar-middle div > a{
    text-decoration: none;
    color:#222222;
}

.navbar-right{
    display:grid;
    position: relative;
    grid-template-columns:repeat(3,1fr);
    justify-self: end;
    justify-items: end;
    grid-gap:5px;
    gap:5px;
}
.navbar-right .multichoice{
    display:flex;
    align-items: center;
    justify-content: center;
    font-size:16pt;
    cursor:pointer;
    position: absolute;
    right:0px;
    margin-left:30px;
    width:35px;
    height:35px;
    background-color: #F2F2F2;
}
.navbar-right .icons{
    display:grid;
    grid-template-columns:1fr 1fr;
    align-items: center;
}
.icons i {
    position: relative;
    left:20px;
    text-align: center;
}
.icons {
    display:grid;
    grid-gap:6px;
    gap:6px;
    position: relative;
    left:40px;
}
.navbar-right button{
    font-family: 'Poppins', sans-serif;
    background-color: #2166B1;
    height:35px;
    color:white;
    border:none;
    position: relative;
    left:70px;
    margin-left:10px;
}
.navbar-right button:focus{
    outline: none;
}

.banner{
    position: relative;
    overflow:hidden;
    margin-bottom:30px;
    margin-top:50px;
}

.banner .header{
    font-size:12vw;
    font-family:  sans-serif;
    position: relative;
    font-weight:bold;
    color:rgba(122,122,122,0.3);
    bottom:20px;
    padding-left:10px;
}
.banner-container{
    display: flex;
    position: relative;
}
.banner-container img {
    position: relative;
    left:40%;
    bottom:50px;
}
.banner-text{
    position: absolute;
    font-family:'Poppins',sans-serif;
    padding-left:100px;
    width:50vw;
    display:grid;

}
.banner-title{
    font-size:2.4vw;
}
.banner-title span{
    display: block;
}
.banner-important{
    font-size:3vw;
    font-weight:800;
}
/*banner plus text*/
.banner-plus{
    position: relative;
    margin-top:20px;
    margin-bottom:10px;
    font-size:1vw;
}
.banner-plus span{
    color:#E84C9B;
}
/* banner buttons*/
.banner-buttons{
    display:grid;
    width:60%;
    grid-template-columns:1fr 1fr 1fr;
    grid-gap:5px;
    gap:5px;
}
.banner-buttons button{
    border:none;
    padding:0.5rem;
    background-color: transparent;
    color:#222E3C;
    font-family:'Poppins',sans-serif;
    cursor: pointer;
}
.banner-buttons button:focus{
    outline:none
}
.banner-buttons .active{

    background-color: #2166B1;
    color:white;
    font-weight: bold;
}
.banner-buttons .normal{
    color:#222E3C;
    border:1px solid #2166B1

}
/*end banner buttons*/

/* start discount kit*/
    .banner .discount-text{
        text-align: center;
        color:#E84C9B;
        margin-right:20px;
    }

    .banner .discount{
        display:grid;
