/*
the container
*/
.howitworks{
    width:80%;
    position: relative;
    margin:0 auto;
    display:grid;
    grid-template-rows:20% 80%;
    min-height:600px;

}

.howitworks .body{
    display:grid;
    grid-template-columns:30% 70%;
    align-items: center;
    grid-column-gap:20px;
    -webkit-column-gap:20px;
            column-gap:20px;
}
.howitworks .body > .imageholder{
    position: relative;
    overflow: hidden;
    max-height:500px;
    border-radius: 10px;
    box-shadow:2px 2px 20px rgba(122,122,122,0.3);
    transition:all 0.5s ease-in-out
}
.howitworks .body > .imageholder:hover{
    box-shadow:2px 2px 20px rgba(122,122,122,0.7);
}
.howitworks .header{
    text-align: center;
}
.howitworks .header-title{
    margin-bottom:10px;
    font-family:'Poppins',sans-serif;
    font-weight:600;
}
.howitworks .header-text{
    position: relative;
    margin:0 auto;
    font-family:'Poppins',sans-serif;
    width:500px;
    font-size:9pt
}
/*
how it works controls
*/
.controls{
    display:grid;
    grid-template-rows:repeat(3,1fr);
    width:300px;
}
.control-item{
    display:grid;
    justify-content:start;
}
.control-item .control-header{
    display:grid;
    width:150px;
    grid-template-columns:20% 70%;
    align-items: center;
    justify-content: center;
    justify-self: start;
    padding:2px;
    cursor: pointer;
}
.control-header .index{
    display:flex;
    align-items: center;
    justify-content: center;
    background-color: #2166B1;
    margin-right:15px;
    color:white;
    height:30px;
    width:30px;
}
.control-header div:nth-child(2){
    margin-left:5px;
}
.control-item .control-text{
    padding-left: 40px;
}
.reasons{
display:grid;
grid-template-columns:70% 30%;
position: relative;
width:100%;
justify-content: center;
align-items: center;
margin-top:150px;
font-family:'Poppins',sans-serif;
}
